@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap);
.header{
    display: flex;
}
.logo > img{
    width: 5rem;
    border-radius: 0.5rem;
    padding-right: 2rem;
}
.header__text{
    font-family: 'Montserrat', sans-serif;
}
.header__text > h1{
    font-weight: 600;
    font-size: 2.2rem;
    margin: 0;
}
.header__text > h3{
    font-size: 1.2rem;
    font-weight: 550;
    margin-top: 0.5rem;
    color: var(--color-light);
}

/* ==================== MEDIA QUERIES (MEDIUM DEVICES) ================== */

@media screen and (max-width: 1024px) {
    .header__text > h1{
        font-size: 1.8rem;
        margin-top: 0.5rem;
    }
    .header__text > h3{
        font-size: 1rem;
        margin-top: 0.75rem;
    }
    
}

/* ==================== MEDIA QUERIES (SMALL DEVICES) ================== */

@media screen and (max-width: 600px) {
    .header__text{
        margin-left: 2%;
    }
    .header__text > h1{
        font-size: 125%;
        margin-top: 2%;
    }
    .header__text > h3{
        font-size: 65%;
        margin-top: 3%;
    }
    .logo{
        width: 15%;
        overflow: visible;
    }
    .logo>img{
        width: 100%;
    }
}
.team__container{
    height: 40rem;
    background: var(--color-white);
    margin-top: 2%;
    border-radius: 0.7rem;
    box-shadow: 0 0 1rem var(--color-bg-variant);
}

.content{
    padding-left: 2%;
    padding-top: 2%;
    width: 96%;
}

.content > h1{
    font-family: 'Montserrat', sans-serif;
    font-size: x-large;
    margin-bottom: 1.5rem;
}

.search__container{
    display: flex;
    width: inherit;
    height: 2.5rem;
    margin-right: 2%;
    border: 1px solid var(--color-bg-variant);
    border-radius: 0.5rem;
    overflow: hidden;
    box-shadow: 0 0 0.25rem var(--color-light);
}

.search__container > input{
    width: 90%;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
    border: none;
    outline: none;
    border-radius: 0.5rem;
}

.search__container > input[type='text']{
    font-size: 1.25rem;
    padding-left: 1rem;
    font-family: 'Montserrat', sans-serif;
}

.sgstns__container{
    width: 80.75%;
    max-height: 45%;
    height: -webkit-max-content;
    height: max-content;
    margin-top: 0.25rem;
    position: absolute;
    overflow: scroll;
    scroll-behavior: smooth;
    background: var(--color-white);
    box-shadow: 0 0 0.5rem var(--color-light);
    border-radius: 0.5rem;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.sgstns__container::-webkit-scrollbar{
    display: none;
}

.add__btn{
    width: 10%;
    min-width: -webkit-max-content;
    min-width: max-content;
    font-family: 'Montserrat', sans-serif;
    font-size: 1rem;
    font-weight: bold;
    color: var(--color-white);
    background: var(--color-primary);
    border: none;
    transition: var(--transition-2);
    -webkit-transform: scale(1, 1.2);
            transform: scale(1, 1.2);
}


.add__btn:hover{
    background: var(--color-primary-variant);
    transition: var(--transition);
}

.add__btn:active{
    font-size: 0.8rem;
    transition: var(--transition);
}

.team__list{
    height: 25rem;
    width: 96%;
    border-radius: 0.5rem;
    box-shadow: 0 0 0.5rem var(--color-light);
    margin-top: 4rem;
    overflow: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.team__list::-webkit-scrollbar{
    display: none;
}

.team_user{
    height: 5rem;
    width: inherit;
    display: flex;
    
}

.initialz{
    background: var(--color-user-data);
    width: 3rem;
    height: 3rem;
    margin: 1rem;
    border-radius: 50%;
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
}

.initialz>p{
    line-height: 0.85rem;
    text-align: center;
    font-size: 1.15rem;
}

.added_users{
    display: flex;
    border: 1px solid var(--color-border-solid);
    transition: var(--transition);
}

.delete{
    font-size: 2rem;
    color: var(--color-primary);
    margin-top: 1.5rem;
    margin-bottom: auto;
    margin-left: auto;
    margin-right: 1.5rem;
    transition: var(--transition-2);
    position: relative;
}

.delete:hover{
    color: var(--color-primary);
    transition: var(--transition);
}

.delete:active{
    font-size: 1.75rem;
    margin-right: 1.65rem;
    margin-top: 1.65rem;
    transition: var(--transition-2);
}

.to_be_added{
    display: flex;
    background: var(--color-user-data);
    border-radius: 0.4rem;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    max-width: 100%;
    height: 1.5rem;
    margin-top: 0.5rem;
    margin-bottom: auto;
    margin-left: 0.5rem;
}

.remove{
    margin-top: 0.125rem;
    font-size: 1.25rem;
    margin-bottom: auto;
    margin-right: 0.25rem;
    margin-left: auto;
}

.search_list{
    display: flex;
    width: none;
    max-width: 50%;
    overflow: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.search_list::-webkit-scrollbar{
    display: none;
}

.user{
    font-family: 'Montserrat', sans-serif;
    padding: 0.2rem 0.5rem;
    display: flex;
    overflow: hidden;
}

/* ==================== MEDIA QUERIES (MEDIUM DEVICES) ================== */

@media screen and (max-width: 1024px) {
    .team__container{
        height: 50rem;
    }
    .team__list{
        height: 35rem;
    }
    .search__container > input{
        width: 85%;
    }
    .sgstns__container{
        width: 76.5%;
    }
    .add__btn{
        width: 15%;
    }
    .add__btn:hover{
        background: var(--color-primary);
    }
    .add__btn:active{
        background: var(--color-primary-variant);
    }
    .content>h1{
        font-size: 100%;
    }
    .delete:hover{
        color: var(--color-primary);
    }
}

/* ==================== MEDIA QUERIES (SMALL DEVICES) ================== */

@media screen and (max-width: 600px) {
    .team__container{
        height: 35rem;
    }
    .team__list{
        height: 25rem;
        margin-top: 2rem;
    }
    .sgstns__container{
        width: 71.5%;
    }
    .search__container{
        height: 1.5rem;
        border-radius: 0.25rem;
    }
    .add__btn{
        font-size: 75%;
    }
    .search__container>input{
        border-radius: 0.25rem;
    }
    .search__container>input[type='text']{
        font-size: 100%;
    }
    .team_user{
        height: 3rem;
    }
    .initialz{
        height: 1.8rem;
        width: 1.8rem;
        margin: 0.6rem;
    }
    .initialz>p{
        font-size: 0.69rem;
        line-height: 0.51rem;
    }
    .delete{
        font-size: 1.2rem;
        margin-top: 0.9rem;
        margin-bottom: auto;
        margin-left: auto;
        margin-right: 0.9rem;
    }
    .delete:active{
        font-size: 1.055rem;
        margin-right: 0.99rem;
        margin-top: 0.99rem;
    }
    .user{
        font-family: 'Montserrat', sans-serif;
        padding: 0.12rem 0.3rem;
        display: flex;
        overflow: hidden;
        font-size: 60%;
    }
    .to_be_added{
        border-radius: 0.4rem;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        max-width: 100%;
        height: 0.9rem;
        margin-top: 0.3rem;
        margin-left: 0.3rem;
    }
    .remove{
        font-size: 75%;
        margin-right: 0.15rem;
    }
}
.recommendation__container{
    position: static;
    position: initial;
    width: inherit;
    height: 5rem;
    border: 1px solid var(--color-border-solid);
    background: var(--color-white);
    display: flex;
    overflow: hidden;
    transition: var(--transition);
}

.recommendation__container:hover{
    background: var(--color-hover);
}

.initials{
    background: var(--color-user-data);
    width: 3rem;
    height: 3rem;
    margin: 1rem;
    border-radius: 50%;
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
}

.initials>p{
    text-align: center;
    -webkit-transform: translateY(-25%);
            transform: translateY(-25%);
    font-size: 1.15rem;
    
}

.user__info{
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
    font-size: 1.3rem;
    display: flex;
    flex-direction: column;

}
#name{
    margin-top: 1rem;
    margin-bottom: 0.25rem;
}
#details{
    font-size: 0.8rem;
    margin: 0;
    padding-right: 1rem;
}

/* ==================== MEDIA QUERIES (MEDIUM DEVICES) ================== */

@media screen and (max-width: 1024px) {
    
    .recommendation__container:hover{
        background: var(--color-white);
    }
}

/* ==================== MEDIA QUERIES (SMALL DEVICES) ================== */

@media screen and (max-width: 600px) {
    .recommendation__container{
        height: 3rem;
    }
    .initials{
        height: 1.8rem;
        width: 1.8rem;
        margin: 0.6rem;
    }
    .initials>p{
        font-size: 0.69rem;
    }
    .user__info{
        font-size: 0.75rem;
    }
    #name{
        margin-top: 0.5rem;
    }
    #details{
        font-size: 0.45rem;
        margin: 0;
    }
}
body{
    margin: 1.5%;
    background: #ededf0;
    background: var(--color-bg);
}
:root{
    --color-bg: #ededf0;
    --color-bg-variant: #9d9ea7;
    --color-primary: #10098b;
    --color-primary-variant: rgb(130, 15, 146);
    --color-white: #fff;
    --color-light: rgb(97, 97, 97);
    --color-border-solid: rgba(97,97,97,0.5);
    --color-user-data: rgba(194, 132, 228, 0.5);
    --color-hover: rgb(186, 186, 186);

    --transition: all 400ms ease;
    --transition-2: all 200ms ease;
}
html{
    scroll-behavior: smooth;
}
